.container {
  padding: 20px;
}

.setting-box {
  width: 330px;
  margin-bottom: 30px;
  border: 1px solid #ddd;
  border-radius: 16px;
  padding: 25px;
  background: linear-gradient(145deg, #ffffff, #f1f1f1);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  max-width: 1000px;
  margin: 0 auto 50px;
}
h2 {
  margin: 0;
}

.label {
  font-weight: 600;
  color: #333;
  margin-bottom: 5px;
}

.input {
  padding: 6px 10px;
  width: 25px;
  text-align: center;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 14px;
}

.select {
  padding: 6px 10px;
  width: 100%;
  text-align: center;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 14px;
}

.button {
  padding: 10px 20px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  height: 44px;
}
